import React from "react";
import { GoogleMap, Marker, InfoBox, Polygon } from "@react-google-maps/api";
import "./styles.css";
import { mapSettings } from "./mapSettings";
import markerSvg from "./marker.svg";
import markerActive from "./markerActive.svg";
import { CardMap } from "../CardMap";
import { useMap } from "./useMap";

const {
  options,
  polygon: { options: polygonOptions },
} = mapSettings[0];

const Map = () => {
  const {
    mapStatus: { isLoaded, loadError },
    mapState: { markers, selected, mapCenter, ariaCriteria },
    regionState: { regionCoordinates },
    mapHandlers: { onMapLoad },
    cardHandlers: { showCard, hideCard, onCardLoad, openObjectPage },
    refs: { infoBoxRef },
  } = useMap();

  if (loadError) return "Ошибка загрузки карты";
  if (!isLoaded) return "Зашрузка карты ...";

  regionCoordinates.forEach((elements) => {
    if (Array.isArray(elements)) {
      elements.forEach((item) => {
        item.lat = Number(item.lat);
        item.lng = Number(item.lng);
      });
    } else {
      elements.lat = Number(elements.lat);
      elements.lng = Number(elements.lng);
    }
  });

  return (
    <div className="Map">
      <GoogleMap
        mapContainerClassName="Map__container"
        zoom={11}
        center={mapCenter}
        options={options}
        onLoad={onMapLoad}
      >
        {markers.map(
          (marker, index) =>
            marker.latitude &&
            marker.longitude && (
              <Marker
                key={`${marker.adress}-${index}`}
                position={{
                  lat: Number(marker?.latitude),
                  lng: Number(marker?.longitude),
                }}
                icon={{
                  url:
                    marker?.data_id === selected?.data_id
                      ? markerActive
                      : markerSvg,
                  scaledSize: new window.google.maps.Size(24, 24),
                  origin: new window.google.maps.Point(0, 0),
                  anhor: new window.google.maps.Point(12, 12),
                }}
                onMouseOver={showCard(marker)}
                onMouseOut={hideCard}
                onClick={openObjectPage(marker)}
              />
            )
        )}

        {selected ? (
          <InfoBox
            containerElement="MapCard__Card"
            className="MapCard__Card"
            position={{
              lat: Number(selected.latitude),
              lng: Number(selected.longitude),
            }}
            onCloseClick={hideCard}
            onLoad={onCardLoad}
          >
            <div ref={infoBoxRef}>
              <CardMap data={selected} />
            </div>
          </InfoBox>
        ) : null}

        {ariaCriteria === "all"
          ? regionCoordinates.map(
              (coordinates) =>
                coordinates.length > 0 && (
                  <Polygon
                    key={coordinates}
                    paths={coordinates}
                    options={polygonOptions}
                  />
                )
            )
          : regionCoordinates.length > 0 && (
              <Polygon paths={regionCoordinates} options={polygonOptions} />
            )}
      </GoogleMap>
      {/* <div className="Map__placesCounters">
        Найдено
        {markers.length > 4
          ? ` ${markers.length} объектов`
          : ` ${markers.length} объекта`}
      </div> */}
    </div>
  );
};
export { Map };
