import { useEffect, useRef } from "react";

export const useScript = (url, needLoadScript) => {
  let script = useRef(null);

  useEffect(() => {
    if (!needLoadScript) return;

    if (!document.body.contains(script.current)) {
      script.current = document.createElement("script");
      script.current.src = url;
      script.current.async = true;
      document.body.appendChild(script.current);
    }

    return () => {
      const google = window.google;
      localStorage.setItem("map", JSON.stringify(google.maps));
      if (document.body.contains(script.current)) {
        document.body.removeChild(script.current);
      }
    };
  }, [needLoadScript, url]);
};
