import React, { useRef, useEffect } from "react";
import { useScript } from "../../hooks/useScript";
import { useMapWithPlaces } from "../../hooks/useMapWithPlaces";

import "./styles.css";

export const MapContent = ({
  latLng,
  mapStyles,
  streetView,
  nameSevenObject,
  addressSevenObject,
}) => {
  const mapContainer = useMapWithPlaces({
    latLng,
    mapStyles,
    id: "map-restaurant",
    zoomLevel: 15,
    streetView,
    nameSevenObject,
    addressSevenObject,
  });
  return <div className="MapContainer" ref={mapContainer}></div>;
};
