import React, { useEffect, useState, useCallback } from "react";

import { Map } from "./components/Map";

const App = () => {
  const [isActiveApp, setIsActive] = useState(true);

  const switchHandler = useCallback(() => {
    setIsActive(!isActiveApp);
  }, [isActiveApp]);

  useEffect(() => {
    const switchMap = document.getElementById("switch-map");
    if (switchMap) {
      switchMap.addEventListener("click", switchHandler);
    }
    return () => {
      if (switchMap) {
        switchMap.removeEventListener("click", switchHandler);
      }
    };
  }, [isActiveApp, switchHandler]);

  return isActiveApp ? (
    <div className="App">
      <Map />
    </div>
  ) : null;
};

export default App;
