import React, { useRef, useState, useCallback, useEffect } from "react";
import { Modal } from "../Modal";
import cn from "classname";
import { MapContent } from "../../module/maps/MapContent";
import {
  stylesShops,
  stylesSchools,
  stylesParkAndHospital,
} from "../../module/maps/styles";
import { CloseModal } from "./CloseModal";
import "./styles.css";
import { Arrow } from "./Arrow";

export const ModalMap = ({
  isOpen,
  onClose,
  latlang: { lat, lng },
  activeIndex,
  nameSevenObject,
  addressSevenObject,
}) => {
  const mapLinks = [
    "Карта",
    "Просмотр Улицы",
    "Школы",
    "Парки и Больницы",
    "Рестораны и Бары",
  ];
  const modalMapContentREf = useRef(null);
  const [activeLink, setActiveLink] = useState(activeIndex);
  const ulRef = useRef(null);

  const scroll = (index) => {
    const tabs = [...ulRef.current.children];
    tabs[index].scrollIntoView({
      behavior: "smooth",
      block: "nearest",
      inline: "start",
    });
  };

  const slideRight = () => {
    scroll(4);
  };

  const slideLeft = () => {
    scroll(0);
  };
  const scrollTo = useCallback((index) => {
    scroll(index);
  }, []);

  const switchMap = useCallback(
    (index) => () => {
      setActiveLink(index);
      setTimeout(() => scrollTo(index), 400);
    },
    [scrollTo]
  );

  useEffect(() => {
    setActiveLink(activeIndex);
  }, [activeIndex]);
  return (
    <Modal isOpen={isOpen} onClose={onClose}>
      <div ref={modalMapContentREf} className="ModalMap__Content">
        <div className="ModalMap__NavigationMenu">
          <button onClick={slideLeft} className="ModalMap__paginationButton">
            <Arrow className="ModalMap_leftArrow" />
          </button>
          <ul ref={ulRef} className="ModalMap__links">
            {mapLinks.map((link, index) => (
              <li key={link} className="ModalMap__link">
                <button
                  onClick={switchMap(index)}
                  className={cn("ModalMap__linkButton", {
                    ModalMap__linkButton_active: index === activeLink,
                    ModalMap__linkButton_inactive: index !== activeLink,
                  })}
                >
                  {link}
                </button>
              </li>
            ))}
          </ul>
          <button onClick={slideRight} className="ModalMap__paginationButton">
            <Arrow className="ModalMap_rightArrow" />
          </button>

          <button className="ModalMap__closeMap" onClick={onClose}>
            <CloseModal className="ModalMap__closeMapIcon" />
          </button>
        </div>

        <div className="ModalMap__map">
          {activeLink === 0 && (
            <MapContent
              latLng={{ lat, lng }}
              nameSevenObject={nameSevenObject}
              addressSevenObject={addressSevenObject}
            />
          )}
          {activeLink === 1 && (
            <MapContent
              latLng={{ lat, lng }}
              mapStyles={stylesSchools}
              streetView={true}
              nameSevenObject={nameSevenObject}
              addressSevenObject={addressSevenObject}
            />
          )}
          {activeLink === 2 && (
            <MapContent
              latLng={{ lat, lng }}
              mapStyles={stylesSchools}
              nameSevenObject={nameSevenObject}
              addressSevenObject={addressSevenObject}
            />
          )}

          {activeLink === 3 && (
            <MapContent
              latLng={{ lat, lng }}
              mapStyles={stylesParkAndHospital}
              nameSevenObject={nameSevenObject}
              addressSevenObject={addressSevenObject}
            />
          )}

          {activeLink === 4 && (
            <MapContent
              latLng={{ lat, lng }}
              mapStyles={stylesShops}
              nameSevenObject={nameSevenObject}
              addressSevenObject={addressSevenObject}
            />
          )}
        </div>
      </div>
    </Modal>
  );
};
