import { useState, useEffect, useCallback } from "react";
import Geocode from "react-geocode";
import { useMemo } from "react";

export const useGeocode = (lat, lng) => {
  const [address, setAddress] = useState("");

  const getAddressFromLatLng = useCallback(() => {
    Geocode.setApiKey(process.env.REACT_APP_GOOGLE_MAPS_API_KEY);
    Geocode.setLanguage("ru");
    Geocode.setRegion("ru");
    Geocode.enableDebug();

    Geocode.fromLatLng(lat, lng).then(
      (response) => {
        const result = response.results[1];
        const address = response.results[1].formatted_address;

        setAddress(address);
      },
      (error) => {
        console.error(error);
      }
    );
  }, [lat, lng]);

  useEffect(() => {
    if ((lat === undefined) | null) return;
    if ((lng === undefined) | null) return;
    getAddressFromLatLng();
  }, [address, getAddressFromLatLng, lat, lng]);

  return useMemo(
    () => ({
      address,
    }),
    [address]
  );
};
