import React, { StrictMode, useContext } from "react";
import { render } from "react-dom";
import App from "./App";
import { Environments } from "./components/Enviroments";

const map = document.getElementById("map");
const modalmap = document.getElementById("modal-map");

class GlobalStore {
  constructor(initalStore) {
    this.state = initalStore;
  }
  setState(state) {
    this.state = state;
  }
  getState() {
    return this.state;
  }
}

const initialStore = new GlobalStore({ id: 0 });

if (map) {
  render(
    <StrictMode>
      <App />
    </StrictMode>,
    map
  );
}

if (modalmap) {
  render(
    <StrictMode>
      <Environments needLoadScript={!map} />
    </StrictMode>,
    modalmap
  );
}
