import { useEffect, useRef } from "react";
import { useGeocode } from "./useGeocode";

export const useMapWithPlaces = ({
  latLng: { lat, lng },
  mapStyles,
  id,
  zoomLevel,
  streetView = false,
  nameSevenObject,
  addressSevenObject,
}) => {
  const latNumber = Number(lat);
  const lngNumber = Number(lng);
  const mapContainer = useRef(null);

  useEffect(() => {
    let map = null;

    const initMap = () => {
      map = new window.google.maps.Map(mapContainer.current, {
        center: new window.google.maps.LatLng(latNumber, lngNumber),
        zoom: zoomLevel,
        mapTypeId: window.google.maps.MapTypeId.ROADMAP,
        styles: mapStyles,
      });
      const markerSeven = new window.google.maps.Marker({
        position: { lat: latNumber, lng: lngNumber },
        map,
        title: "Hello World!",
        icon: "/modal-map-marker.png",
      });
      const infowindow = new window.google.maps.InfoWindow({
        content: `${nameSevenObject ? `<h3>${nameSevenObject}</h3>` : ""}
          <div>${addressSevenObject}</div>
          <a target="_blank" jstcache="6" href="https://maps.google.com/maps?ll=${lat},${lng}&amp;z=15&amp;t=m&amp;hl=ru-RU&amp;gl=US&amp;mapclient=apiv3&amp;cid=6494927348157778395"> <span>Показать на Google Картах</span> </a>
        `,
        maxWidth: 200,
      });

      markerSeven.addListener("click", () => {
        if (nameSevenObject) infowindow.open(map, markerSeven);
      });
      if (!streetView) return;

      const panorama = new window.google.maps.StreetViewPanorama(
        mapContainer.current,
        {
          position: { lat: latNumber, lng: lngNumber },
          pov: {
            heading: 170,
            pitch: 0,
          },
        }
      );

      map.setStreetView(panorama);
    };
    initMap();
  }, [
    addressSevenObject,
    id,
    lat,
    latNumber,
    lng,
    lngNumber,
    mapStyles,
    nameSevenObject,
    streetView,
    zoomLevel,
  ]);
  return mapContainer;
};
