import React, { useState, useCallback } from "react";
import { Marker, Price, Square, Rooms, Bed, House, Bathrooms } from "./icons";

import "./styles.css";
import { Slider } from "./Slider/Slider";

const CardMap = ({ data, refCard }) => {
  const [imageIndex, setImageIndex] = useState(0);

  const {
    id,
    address,
    all_rooms,
    area_id,
    bath_rooms,
    bed_rooms,
    description,
    name,
    price,
    square,
    minSquare,
    maxSquare,
    minPrice,
    maxPrice,
    ItemsCount,
    imagesApi,
  } = data;
  const statusImage = "prod";

  return (
    <div ref={refCard} className="CardMap">
      <div className="CardMap__images">
        <div className="CardMap__imageContainer">
          {imagesApi && statusImage !== "test" ? (
            <Slider images={imagesApi} />
          ) : (
            // <img
            // className="CardMap__image"
            // src={imagesApi[imageIndex]}
            // alt="img"
            // />
            <img className="CardMap__image" src="/img.png" alt="img" />
          )}
        </div>

        <div className="CardMap__imageButtonPagination"></div>
      </div>

      <div className="CardMap__content">
        <div className="CardMap__title">{name}</div>
        {ItemsCount > 0 && (
          <div className="CardMap__objectQuantity">
            {ItemsCount > 4
              ? ` ${ItemsCount} объектов`
              : ` ${ItemsCount} объекта`}
          </div>
        )}

        <div className="CardMap__address">
          <Marker /> {address}
        </div>

        {(square || all_rooms || bed_rooms || bath_rooms) && (
          <div className="CardMap__objectPropertes">
            {square && (
              <div className="CardMap__objectProperty">
                <House /> <span>{square} м²</span>
              </div>
            )}
            {all_rooms && (
              <div className="CardMap__objectProperty">
                <Rooms /> <span>{all_rooms}</span>
              </div>
            )}
            {bed_rooms && (
              <div className="CardMap__objectProperty">
                <Bed /> <span>{bed_rooms}</span>
              </div>
            )}
            {bath_rooms && (
              <div className="CardMap__objectProperty">
                <Bathrooms /> <span>{bath_rooms}</span>
              </div>
            )}
          </div>
        )}
        {price && (
          <div className="CardMap__price">
            {Number(price).toLocaleString("ru-RU", {
              style: "currency",
              currency: "RUB",
              minimumFractionDigits: 0,
              maximumFractionDigits: 0,
            })}
          </div>
        )}

        {minSquare && maxSquare && (
          <div className="CardMap__objectArea">
            <Square /> {minSquare} м² — {maxSquare} м²
          </div>
        )}

        {minPrice && maxPrice && (
          <div className="CardMap__prices">
            <div className="CardMap__price">
              <span>
                <Price /> От:
              </span>
              {Number(minPrice).toLocaleString("ru-RU", {
                style: "currency",
                currency: "RUB",
                minimumFractionDigits: 0,
                maximumFractionDigits: 0,
              })}
            </div>
            <div className="CardMap__price">
              <span className="CardMap__priceMax">До:</span>
              {Number(maxPrice).toLocaleString("ru-RU", {
                style: "currency",
                currency: "RUB",
                minimumFractionDigits: 0,
                maximumFractionDigits: 0,
              })}
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export { CardMap };
