import { useState, useCallback, useRef, useEffect, useMemo } from "react";
import { useLoadScript } from "@react-google-maps/api";
import { mapSettings } from "./mapSettings";
import { Api } from "../../api";
import { useOnClickOutside } from "./useOnClickOutside";

const { libraries, center } = mapSettings[0];

const useMap = () => {
  /* Load Map*/
  const { isLoaded, loadError } = useLoadScript({
    googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
    libraries,
  });

  /* markers */
  const [markers, setMarkers] = useState([]);
  const [selected, setSelected] = useState(null);

  /* region */
  const [regionCoordinates, setRegionCoordinates] = useState([]);

  /* map center */
  const [mapCenter, setMapCenter] = useState(center);

  /* refs */
  const mapRef = useRef();
  const onMapLoad = useCallback((map) => {
    mapRef.current = map;
  }, []);

  /* ariaCriteria*/

  const [ariaCriteria, setAriaCriteria] = useState("");

  const infoBoxRef = useRef(null);
  const onCardLoad = useCallback(
    (card) => {
      if (selected) {
        const widthMap = document.querySelector(".Map").clientWidth;
        const heightMap = document.querySelector(".Map").clientHeight;

        const selectedY = (20 + Number(selected.latitude) * heightMap) / 180;
        const selectedX = 20 + Number(selected.longitude * widthMap) / 360;

        card.alignBottom = true;

        // card.pixelOffset.width = (widthMap - selectedX) / 20;
        // card.pixelOffset.height = (heightMap - selectedY) / 20;

        card.pixelOffset.width = (widthMap - selectedX) / 20;
        card.pixelOffset.height = (heightMap - selectedY) / 20;
        // card.boxClass += "  CardMap__infoBox_pos";

        // if (selected.longitude > mapCenter.lng) {
        //   if (selected.latitude > mapCenter.lat) {
        //     card.pixelOffset.width = -40;
        //     card.pixelOffset.height = -40;
        //   } else {
        //     card.pixelOffset.width = -40;
        //     card.pixelOffset.height = 40;
        //   }
        // } else {
        //   if (selected.latitude > mapCenter.lat) {
        //     card.pixelOffset.width = 40;
        //     card.pixelOffset.height = -40;
        //   } else {
        //     card.pixelOffset.width = 40;
        //     card.pixelOffset.height = 40;
        //   }
        // }
      }
    },
    [selected]
  );

  useEffect(() => {
    const getObjects = async () => {
      const jsonDataRequest = document.querySelector(
        'meta[name="data-backend"]'
      ).content;

      const dataRequest = JSON.parse(jsonDataRequest);
      let data = { ...dataRequest.filter };

      const objects = await await (await Api.getObjects(data)).json();

      if (data.area) {
        setAriaCriteria(data.area);
        setRegionCoordinates(objects?.areas?.geometry?.coordinates);
        setMarkers(objects.objects);
      } else {
        setAriaCriteria("all");
        setRegionCoordinates(
          objects?.areas?.map(({ geometry }) => geometry.coordinates)
        );

        setMarkers([...objects.objects, ...objects.zhks]);
      }
    };

    getObjects();
  }, []);

  /* switch card */
  const hideCard = useCallback(() => {
    setSelected(null);
  }, []);

  const showCard = useCallback(
    (selected) => () => {
      setSelected(selected);
    },
    []
  );

  const openObjectPage = useCallback(
    (marker) => () => {
      window.open(marker.url, "__pageObject");
    },
    []
  );

  /* useOnClickOutside*/
  useOnClickOutside(infoBoxRef, hideCard);

  /*change card onHover */
  const handleCardChange = useCallback(
    (e) => {
      if (e.target.id !== "cards" && markers.length > 0) {
        let targetCard = e.target.closest('div[data-name="card"]');

        const targetMarker = markers.filter(
          (marker) => marker?.data_id == targetCard.dataset.id
        )[0];

        if (targetMarker?.latitude && targetMarker?.longitude) {
          console.log(targetMarker);
          setMapCenter({
            lat: Number(targetMarker.latitude),
            lng: Number(targetMarker.longitude),
          });
          setSelected(targetMarker);
        }
      } else {
        setSelected(null);
      }
    },
    [markers]
  );

  useEffect(() => {
    const cards = document.getElementById("cards");
    cards.addEventListener("mouseover", handleCardChange);
    return () => cards.removeEventListener("mouseover", handleCardChange);
  }, [handleCardChange]);

  useEffect(() => {
    const getCenterRegion = (regionCoordinates) => {
      let LatMax = regionCoordinates[0].lat;
      let LngMax = regionCoordinates[0].lng;
      let LatMin = regionCoordinates[0].lat;
      let LngMin = regionCoordinates[0].lng;

      regionCoordinates.forEach(({ lat, lng }) => {
        LatMax = lat > LatMax ? lat : LatMax;
        LngMax = lng > LngMax ? lng : LngMax;

        LatMin = lat < LatMin ? lat : LatMin;

        LngMin = lng < LngMin ? lng : LngMin;
      });
      const centerLat = (LatMax + LatMin) / 2;
      const centerLng = (LngMax + LngMin) / 2;

      return {
        lat: centerLat,
        lng: centerLng,
      };
    };

    // if (regionCoordinates.length > 0) {
    //   if (ariaCriteria !== "all") {
    //     setMapCenter(getCenterRegion(regionCoordinates));
    //   } else {
    //     setMapCenter(getCenterRegion(regionCoordinates[2]));
    //   }
    // }
  }, [ariaCriteria, regionCoordinates]);

  return useMemo(
    () => ({
      mapStatus: { isLoaded, loadError },
      mapState: { markers, selected, mapCenter, ariaCriteria },
      regionState: { regionCoordinates },
      mapHandlers: { onMapLoad },
      cardHandlers: { showCard, hideCard, onCardLoad, openObjectPage },
      refs: { infoBoxRef },
    }),
    [
      isLoaded,
      loadError,
      markers,
      selected,
      mapCenter,
      ariaCriteria,
      regionCoordinates,
      onMapLoad,
      showCard,
      hideCard,
      onCardLoad,
      openObjectPage,
    ]
  );
};

export { useMap };
