import React, { useState, useCallback, useEffect } from "react";
import { Environment } from "../Enviroments/Enviroment/Enivroment";
import { ModalMap } from "../../components/ModalMap";
import { useScript } from "../../hooks/useScript";
import { useGeocode } from "../../hooks/useGeocode";
import { map, hospital, coffee, schools, streetView } from "./images";
import "./styles.css";

export const Environments = ({ needLoadScript }) => {
  useScript(
    `https://maps.googleapis.com/maps/api/js?key=${process.env.REACT_APP_GOOGLE_MAPS_API_KEY}&libraries=places`,
    needLoadScript
  );

  const environments = [
    {
      name: "map",
      title: "Карта",
      description: "Посмотреть карту",
      image: map,
    },
    {
      name: "street",
      title: "Просмотр Улицы",
      description: "Посмотреть улицу в 3D",
      image: streetView,
    },
    {
      name: "school",
      title: "Школы",
      description: "Посмотреть школы поблизости",
      image: schools,
    },
    {
      name: "parks",
      title: "Парки и больницы",
      description: "Посмотреть больницы и парки поблизости",
      image: hospital,
    },
    {
      name: "Restaurants and bars ",
      title: "Рестораны и бары",
      description: "Посмотреть бары и рестораны поблизости",
      image: coffee,
    },
  ];

  const [{ isOpenModal, activeIndex }, setIsOpenModal] = useState({
    isOpenModal: false,
    activeIndex: 0,
  });

  const openModal = useCallback(
    (index) =>
      setIsOpenModal({
        isOpenModal: true,
        activeIndex: index,
      }),
    [setIsOpenModal]
  );
  const closeModal = useCallback(
    () =>
      setIsOpenModal({
        isOpenModal: false,
      }),
    [setIsOpenModal]
  );
  const lat = document.querySelector('meta[name="item-lat"]').content;
  const lng = document.querySelector('meta[name="item-lon"]').content;
  const nameSevenObject =
    document.querySelector('meta[name="item-name"]')?.content || null;
  const addressSevenObject = useGeocode(lat, lng).address;

  return (
    <div className="Environments">
      <div className="Environments__wrapper">
        {environments.map(({ name, title, description, image }, index) => (
          <Environment
            name={name}
            onClick={openModal.bind(this, index)}
            title={title}
            description={description}
            image={image}
            key={index}
          />
        ))}
      </div>

      <ModalMap
        onClose={closeModal}
        isOpen={isOpenModal}
        activeIndex={activeIndex}
        latlang={{ lat, lng }}
        nameSevenObject={nameSevenObject}
        addressSevenObject={addressSevenObject}
      />
    </div>
  );
};
